<template>
	<view class="container">
		<!-- Â∫ïÈÉ®‰ø°ÊÅØÊ†è -->
		<view class="bottom-info">
			<!-- ÂΩìÂâç‰ΩçÁΩÆÊòæÁ§∫ -->
			<view class="current-location">
				<text class="location-label">ÂΩìÂâç‰ΩçÁΩÆÔºö</text>
				<text class="location-text">{{ address }}</text>
			</view>
		</view>
		
		<!-- Âú∞ÂõæËßÜÂõæ -->
		<view class="map-view">
			<map id="myMap" :latitude="latitude" :longitude="longitude" :markers="markers" :polyline="polyline" :show-location="true" style="width: 100%; height: 100%;"></map>
		</view>
		
		<!-- ÊµÆÂä®ÊåâÈíÆ - Áî®‰∫éÊâìÂºÄÂØºËà™ÂºπÁ™ó -->
		<view class="float-action-btn" @tap="openNavDialog">
			<text>+</text>
		</view>
		
		<!-- ÂØºËà™ÂºπÁ™ó -->
		<view v-if="showNavDialog" class="nav-dialog-overlay" @tap="closeNavDialog">
			<view class="nav-dialog" @tap.stop>
				<view class="nav-dialog-header">
					<text class="nav-dialog-title">ËÆæÁΩÆÂØºËà™</text>
					<view class="nav-dialog-close" @tap="closeNavDialog">√ó</view>
				</view>
				
				<view class="nav-dialog-content">
					<!-- ÊêúÁ¥¢Âå∫Âüü -->
					<view class="search-card">
						<view class="address-group">
							<view class="location-tag">
								<text class="location-text">Ëµ∑</text>
							</view>
							<view class="address-input-group">
								<text class="address-label">Ëµ∑ÁÇπ</text>
								<input class="address-input" type="text" v-model="startPoint.name" placeholder="ÂΩìÂâç‰ΩçÁΩÆ" disabled />
							</view>
						</view>
						
						<view class="divider-line"></view>
						
						<view class="address-group">
							<view class="location-tag">
								<text class="location-text">Áªà</text>
							</view>
							<view class="address-input-group">
								<text class="address-label">ÁªàÁÇπ</text>
								<input class="address-input" type="text" v-model="endPoint.name" placeholder="ËØ∑ËæìÂÖ•ÁªàÁÇπ" @input="onEndInput"/>
							</view>
						</view>
					</view>
					
					<!-- ÊêúÁ¥¢ÁªìÊûú‰∏ãÊãâÊ°Ü - ÈõÜÊàêÂú®ÂºπÁ™óÂÜÖÈÉ® -->
					<view v-if="showSearchResults" class="search-results">
						<view v-for="(tip, index) in searchResults" :key="index" class="search-result-item" @tap="selectSearchResult(tip)">
							<view class="result-icon">
								<text class="place-text">üìç</text>
							</view>
							<view class="result-content">
								<text class="search-result-name">{{ tip.name }}</text>
								<text class="search-result-address">{{ tip.address }}</text>
							</view>
						</view>
					</view>
					
					<!-- Âá∫Ë°åÊñπÂºèÈÄâÊã© -->
					<view class="mode-selector">
						<text class="selector-title">ÈÄâÊã©Âá∫Ë°åÊñπÂºè</text>
						<picker @change="onModeChange" :range="modes" range-key="name" :value="modeIndex">
							<view class="picker">
								{{ modes[modeIndex].name }}
								<view class="picker-arrow"></view>
							</view>
						</picker>
					</view>
				</view>
				
				<view class="nav-dialog-footer">
					<button v-if="!isNavigating" class="nav-button start-nav" @tap="confirmNavigation">ÂºÄÂßãÂØºËà™</button>
					<button v-if="isNavigating" class="nav-button stop-nav" @tap="stopNavigation">ÁªìÊùüÂØºËà™</button>
					<text v-if="isNavigating" class="nav-status">ÂØºËà™‰∏≠...</text>
				</view>
			</view>
		</view>
		

	</view>
</template>

<script>
		// ÂºïÂÖ•È´òÂæ∑Âú∞ÂõæSDK
	const { AMapWX } = require('../../libs/amap-wx.130.js')
	export default {
		data() {
			return {
				latitude: 0,
				longitude: 0,
				address: 'ÂÆö‰Ωç‰∏≠...',
				markers: [],
				polyline: [], // Ë∑ØÁ∫øÊï∞ÊçÆ
				amapKey: '870dfd4461a0542da7d89cbf995185ca',
				modes: [
					{name: 'È©æËΩ¶', value: 'driving'},
					{name: 'Ê≠•Ë°å', value: 'walking'}
				],
				startPoint: {name: 'ÂΩìÂâç‰ΩçÁΩÆ', latitude: 0, longitude: 0},
				endPoint: {name: '', latitude: 0, longitude: 0},
				modeIndex: 0,
				selectedMode: 'driving',
				searchResults:[],
				showSearchResults:false,
				currentSearchType:'', //start Êàñ end
				isNavigating: false, // ÊòØÂê¶Ê≠£Âú®ÂØºËà™
				navigationInterval: null, // ÂØºËà™ÂÆöÊó∂Âô®
				navigationPoints: [], // ÂØºËà™ËøáÁ®ã‰∏≠ÁöÑ‰ΩçÁΩÆÁÇπ
				navigationStartTime: null, // ÂØºËà™ÂºÄÂßãÊó∂Èó¥
				navigationEndTime: null, // ÂØºËà™ÁªìÊùüÊó∂Èó¥
				showNavDialog: false // ÂØºËà™ÂºπÁ™óÊòæÁ§∫Áä∂ÊÄÅ
			}
		},
		onLoad() {
			this.loadMap();
			this.fetchMapData(); // Âä†ËΩΩÈ°µÈù¢Êó∂Ëé∑ÂèñMongoDBÊï∞ÊçÆ
		},
		methods: {
			// ‰ªéMongoDBËé∑ÂèñÊï∞ÊçÆ
			fetchMapData() {
				// Ê≥®ÊÑèÔºöÂæÆ‰ø°Â∞èÁ®ãÂ∫è‰∏≠ÈúÄË¶ÅÈÖçÁΩÆÂêàÊ≥ïÂüüÂêçÊàñ‰ΩøÁî®ÂºÄÂèëÁéØÂ¢ÉÁöÑ‰∏çÊ†°È™åÂüüÂêçÈÄâÈ°π
				uni.request({
					url: 'http://localhost:3000/api/getMapData',
					method: 'GET',
					success: (res) => {
					console.log('‰ªéMongoDBËé∑ÂèñÁöÑÊï∞ÊçÆ:', res.data);
					// Â§ÑÁêÜËé∑ÂèñÂà∞ÁöÑÊï∞ÊçÆ
					},
					fail: (err) => {
						console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', err);
					}
				});
			},

			// ‰øùÂ≠òÊï∞ÊçÆÂà∞MongoDB
			saveMapData(data) {
				uni.request({
					url: 'http://localhost:3000/api/saveMapData',
					method: 'POST',
					data: data,
					success: (res) => {
					console.log('‰øùÂ≠òÊï∞ÊçÆÊàêÂäü:', res.data);
					},
					fail: (err) => {
						console.error('‰øùÂ≠òÊï∞ÊçÆÂ§±Ë¥•:', err);
					}
				});
			},
			// ÈÄâÊã©Âá∫Ë°åÊñπÂºèÂèòÂåñ
			onModeChange(e) {
				this.modeIndex = e.detail.value;
				this.selectedMode = this.modes[this.modeIndex].value;
				console.log('ÈÄâÊã©ÁöÑÂá∫Ë°åÊñπÂºèÔºö', this.selectedMode);
				// ÂàáÊç¢Âá∫Ë°åÊñπÂºèÂêéÔºåÈáçÊñ∞Ëé∑ÂèñË∑ØÁ∫ø
				this.getRouteByMode();
			},
			loadMap() {
				// ÂàõÂª∫Âú∞ÂõæÂÆû‰æã
				const amapwx = new AMapWX({key: this.amapKey})
				// ‰ΩøÁî® getRegeo ÊñπÊ≥ïËé∑ÂèñÂΩìÂâç‰ΩçÁΩÆÂíåÂú∞ÂùÄ‰ø°ÊÅØ
				amapwx.getRegeo({
					// Â¶ÇÊûú‰∏çÊèê‰æõ location ÂèÇÊï∞ÔºåSDK ‰ºöËá™Âä®Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ
					success: (res) => {
						if (res && res[0]) {
							// Êõ¥Êñ∞Âú∞ÂùÄ‰ø°ÊÅØ
							this.address = res[0].name
							// Êõ¥Êñ∞ÁªèÁ∫¨Â∫¶
							this.latitude = res[0].latitude
							this.longitude = res[0].longitude
							// Ê∑ªÂä†Ê†áËÆ∞ÁÇπ
							this.markers = [{
								id: 0,
								latitude: res[0].latitude,
								longitude: res[0].longitude,
								width: 30,
								height: 30,
	
							}]
						} else {
							this.address = 'Êú™ËÉΩËé∑ÂèñËØ¶ÁªÜÂú∞ÂùÄ'
						}
					},
					fail: (err) => {
						console.log('Ëé∑Âèñ‰ΩçÁΩÆÂíåÂú∞ÂùÄÂ§±Ë¥•Ôºö', err)
						this.address = 'Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•'
					}
				})
			},
			// ÈÄâÊã©ÁªàÁÇπ
			selectEndPoint() {
				this.currentSearchType = 'end';
				if (this.endPoint.name) {
					this.getInputTips(this.endPoint.name);
				}
			},
			
			// ÊâìÂºÄÂØºËà™ÂºπÁ™ó
			openNavDialog() {
				this.showNavDialog = true;
			},
			
			// ÂÖ≥Èó≠ÂØºËà™ÂºπÁ™ó
			closeNavDialog() {
				this.showNavDialog = false;
			},
			
			// Á°ÆËÆ§ÂØºËà™ËÆæÁΩÆÂπ∂ÂºÄÂßãÂØºËà™
			confirmNavigation() {
				if (!this.endPoint.latitude || !this.endPoint.longitude) {
					uni.showToast({
						title: 'ËØ∑ÂÖàÈÄâÊã©ÁªàÁÇπ',
						icon: 'none'
					});
					return;
				}
				
				this.closeNavDialog();
				this.startNavigation();
			},
			
			// ÁªàÁÇπËæìÂÖ•‰∫ã‰ª∂
			onEndInput(e) {
				const keywords = e.detail.value;
				this.currentSearchType = 'end';
				if (keywords.length > 0) {
					this.getInputTips(keywords);
				} else {
					this.showSearchResults = false;
				}
			},
			
			// Ëé∑ÂèñËæìÂÖ•ÊèêÁ§∫ÔºàÊ®°Á≥äÊêúÁ¥¢Ôºâ
			getInputTips(keywords) {
				const amapwx = new AMapWX({key: this.amapKey});
				amapwx.getInputtips({
				keywords: keywords,
				location: '', // ‰ª•ÂΩìÂâç‰ΩçÁΩÆ‰∏∫‰∏≠ÂøÉ
				citylimit: true, // ÈôêÂà∂ÂüéÂ∏Ç
				success: (res) => {
					this.searchResults = res.tips;
					this.showSearchResults = this.searchResults.length > 0;
				},
				fail: (err) => {
					console.log('Ëé∑ÂèñËæìÂÖ•ÊèêÁ§∫Â§±Ë¥•Ôºö', err);
				}
				});
			},
			
			// ÈÄâÊã©ÊêúÁ¥¢ÁªìÊûú
			selectSearchResult(tip) {
				console.log('ÈÄâÊã©ÁöÑÊêúÁ¥¢ÁªìÊûúÔºö', tip);
				// ÈöêËóèÊêúÁ¥¢ÁªìÊûú
				this.showSearchResults = false;
				// Ê†πÊçÆÂΩìÂâçÊêúÁ¥¢Á±ªÂûãËÆæÁΩÆËµ∑ÁÇπÊàñÁªàÁÇπ
				if (this.currentSearchType === 'end') {
					// Ëé∑ÂèñËØ¶ÁªÜÂú∞ÂùÄ‰ø°ÊÅØ‰ª•Ëé∑ÂèñÁªèÁ∫¨Â∫¶
					const [longitude, latitude] = tip.location.split(',').map(Number);
					this.endPoint.latitude = latitude;
					this.endPoint.longitude = longitude;
					// Ê∑ªÂä†ÁªàÁÇπÊ†áËÆ∞
					this.addEndMarker();
				}
			},
			
			// Ê∑ªÂä†ÁªàÁÇπÊ†áËÆ∞
			addEndMarker() {
				console.log('ÂáÜÂ§áÊ∑ªÂä†ÁªàÁÇπÊ†áËÆ∞ÔºåÂΩìÂâçendPoint:', this.endPoint);
				// Á°Æ‰øùendPointÊúâÊúâÊïàÁöÑÁªèÁ∫¨Â∫¶
				if (this.endPoint && typeof this.endPoint.latitude === 'number' && typeof this.endPoint.longitude === 'number') {
					// ÂÖàÊ∏ÖÁ©∫Èô§‰∫ÜÂΩìÂâç‰ΩçÁΩÆ‰ª•Â§ñÁöÑÊ†áËÆ∞
					const currentLocationMarker = this.markers.find(marker => marker.id === 0);
					this.markers = currentLocationMarker ? [currentLocationMarker] : [];
					 
					// Ê∑ªÂä†ÁªàÁÇπÊ†áËÆ∞
					this.markers.push({
						id: 1,
						latitude: this.endPoint.latitude,
						longitude: this.endPoint.longitude,
						width: 30,
						height: 30,

					});
					console.log('Ê∑ªÂä†ÁªàÁÇπÊ†áËÆ∞ÊàêÂäüÔºö', this.markers);
					// Ë∞ÉÁî®Ë∑ØÁ∫øËßÑÂàí
					this.getRouteByMode();
				} else {
					console.log('ÁªàÁÇπÁªèÁ∫¨Â∫¶Êó†ÊïàÊàñÊú™ÂÆö‰πâ');
				}
			},
			
			// ÂºÄÂßãÂØºËà™
			startNavigation() {
				if (!this.endPoint.latitude || !this.endPoint.longitude) {
					uni.showToast({
						title: 'ËØ∑ÂÖàÈÄâÊã©ÁªàÁÇπ',
						icon: 'none'
					});
					return;
				}
				
				this.isNavigating = true;
				this.navigationStartTime = new Date();
				this.navigationPoints = [];
				
				// Á´ãÂç≥Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ‰Ωú‰∏∫Á¨¨‰∏Ä‰∏™ÁÇπ
				this.getCurrentLocationAndMark();
				
				// ËÆæÁΩÆÂÆöÊó∂Âô®ÔºåÊØè10ÂàÜÈíüËé∑Âèñ‰∏ÄÊ¨°‰ΩçÁΩÆ
				const TEN_MINUTES = 10 * 60 * 1000;
				this.navigationInterval = setInterval(() => {
					this.getCurrentLocationAndMark();
				}, TEN_MINUTES);
				
				uni.showToast({
					title: 'ÂºÄÂßãÂØºËà™',
					icon: 'success'
				});
			},
			
			// ÁªìÊùüÂØºËà™
			stopNavigation() {
				this.isNavigating = false;
				this.navigationEndTime = new Date();
				
				// Ê∏ÖÈô§ÂÆöÊó∂Âô®
				if (this.navigationInterval) {
					clearInterval(this.navigationInterval);
					this.navigationInterval = null;
				}
				
				// ÊúÄÂêéÂÜçËé∑Âèñ‰∏ÄÊ¨°‰ΩçÁΩÆ
				this.getCurrentLocationAndMark();
				
				// ‰øùÂ≠òÂØºËà™ËÆ∞ÂΩïÂà∞Êï∞ÊçÆÂ∫ì
				this.saveNavigationRecord();
				
				uni.showToast({
					title: 'ÂØºËà™Â∑≤ÁªìÊùü',
					icon: 'success'
				});
			},
			
			// Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆÂπ∂Ê†áÁÇπ
			getCurrentLocationAndMark() {
				const amapwx = new AMapWX({key: this.amapKey});
				
				amapwx.getRegeo({
					success: (res) => {
						if (res && res[0]) {
							const currentPosition = {
								latitude: res[0].latitude,
								longitude: res[0].longitude,
								address: res[0].name,
								timestamp: new Date().getTime(),
								time: new Date().toLocaleString()
							};
							
							// Ê∑ªÂä†Âà∞ÂØºËà™ÁÇπÊï∞ÁªÑ
							this.navigationPoints.push(currentPosition);
							
							// ÂàõÂª∫Êñ∞ÁöÑÊ†áËÆ∞ÁÇπ
							const newMarker = {
								id: Date.now(), // ‰ΩøÁî®Êó∂Èó¥Êà≥‰Ωú‰∏∫ÂîØ‰∏ÄID
								latitude: currentPosition.latitude,
								longitude: currentPosition.longitude,
								width: 20,
								height: 20,
								
								label: {
									content: currentPosition.time.split(' ')[1], // ÊòæÁ§∫Êó∂Èó¥
									color: '#ff0000',
									fontSize: 10
								}
							};
							
							// Ê∑ªÂä†Âà∞Ê†áËÆ∞Êï∞ÁªÑ
							this.markers.push(newMarker);
							
							console.log('Ê∑ªÂä†ÂØºËà™ÁÇπÔºö', currentPosition);
						}
					},
					fail: (err) => {
						console.error('Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•Ôºö', err);
					}
				});
			},
			
			// ‰øùÂ≠òÂØºËà™ËÆ∞ÂΩïÂà∞MongoDB
			saveNavigationRecord() {
				const navigationRecord = {
					startTime: this.navigationStartTime,
					endTime: this.navigationEndTime,
					duration: (this.navigationEndTime - this.navigationStartTime) / 1000, // ÊåÅÁª≠Êó∂Èó¥ÔºàÁßíÔºâ
					startPoint: this.startPoint,
					endPoint: this.endPoint,
					points: this.navigationPoints,
					mode: this.selectedMode
				};
				
				console.log('‰øùÂ≠òÂØºËà™ËÆ∞ÂΩïÔºö', navigationRecord);
				
				// Ë∞ÉÁî®‰øùÂ≠òÊï∞ÊçÆÁöÑÊñπÊ≥ï
				this.saveMapData({
					type: 'navigation_record',
					data: navigationRecord,
					createdAt: new Date()
				});
			},
			
			// Ê†πÊçÆÈÄâÊã©ÁöÑÂá∫Ë°åÊñπÂºèËé∑ÂèñË∑ØÁ∫ø
			getRouteByMode() {
				if (!this.endPoint.latitude || !this.endPoint.longitude) {
					return;
				}
				
				const amapwx = new AMapWX({key: this.amapKey});
				const origin = `${this.longitude},${this.latitude}`;
				const destination = `${this.endPoint.longitude},${this.endPoint.latitude}`;
				
				switch (this.selectedMode) {
					case 'driving':
						this.getDrivingRoute(amapwx, origin, destination);
						break;
					case 'walking':
						this.getWalkingRoute(amapwx, origin, destination);
						break;
					// case 'riding':
					// 	this.getRidingRoute(amapwx, origin, destination);
					// 	break;
				}
			},
			
			// Ëé∑ÂèñÈ©æËΩ¶Ë∑ØÁ∫ø
			getDrivingRoute(amapwx, origin, destination) {
				amapwx.getDrivingRoute({
					origin: origin,
					destination: destination,
					success: (res) => {
						console.log('È©æËΩ¶Ë∑ØÁ∫øÔºö', res);
						this.drawRoute(res);
					},
					fail: (err) => {
						console.log('Ëé∑ÂèñÈ©æËΩ¶Ë∑ØÁ∫øÂ§±Ë¥•Ôºö', err);
					}
					});
			},
			
			//Ëé∑ÂèñÊ≠•Ë°åË∑ØÁ∫ø
			getWalkingRoute(amapwx, origin, destination) {
				amapwx.getWalkingRoute({
				origin: origin,
				destination: destination,
				success: (res) => {
					console.log('Ê≠•Ë°åË∑ØÁ∫øÔºö', res);
					this.drawRoute(res);
				},
				fail: (err) => {
					console.log('Ëé∑ÂèñÊ≠•Ë°åË∑ØÁ∫øÂ§±Ë¥•Ôºö', err);
				}
				});
			},
			
			// Ëé∑ÂèñÈ™ëË°åË∑ØÁ∫ø
			// getRidingRoute(amapwx, origin, destination) {
			// 	amapwx.getRidingRoute({
			// 		origin: origin,
			// 		destination: destination,
			// 		success: (res) => {
			// 			console.log('È™ëË°åË∑ØÁ∫øÔºö', res);
			// 			this.drawRoute(res);
			// 		},
			// 		fail: (err) => {
			// 			console.log('Ëé∑ÂèñÈ™ëË°åË∑ØÁ∫øÂ§±Ë¥•Ôºö', err);
			// 		}
			// 	});
			// },
			
			// ÁªòÂà∂Ë∑ØÁ∫ø
			drawRoute(res) {
				if (!res.paths || res.paths.length === 0) {
					console.log('Êó†Ë∑ØÁ∫øÊï∞ÊçÆ');
					return;
				}
				
				// Ê∏ÖÁ©∫‰πãÂâçÁöÑË∑ØÁ∫ø
				this.polyline = [];
				
				// Ëé∑ÂèñÁ¨¨‰∏ÄÊù°Ë∑ØÁ∫ø
				const path = res.paths[0];
				
				// Ëß£ÊûêË∑ØÁ∫øÂùêÊ†áÁÇπ
				const polylinePoints = [];
				if (path.steps && path.steps.length > 0) {
					for (let i = 0; i < path.steps.length; i++) {
						const step = path.steps[i];
						// Ëß£ÊûêpolylineÂ≠óÁ¨¶‰∏≤‰∏∫ÂùêÊ†áÁÇπÊï∞ÁªÑ
						const points = this.parsePolyline(step.polyline);
						polylinePoints.push(...points);
					}
				} else if (path.polyline) {
					// Êúâ‰∫õAPIÁâàÊú¨Áõ¥Êé•ËøîÂõûpolylineÂ≠óÁ¨¶‰∏≤
					const points = this.parsePolyline(path.polyline);
					polylinePoints.push(...points);
				}
				
				if (polylinePoints.length > 0) {
					// ÂàõÂª∫Ë∑ØÁ∫øÂØπË±°
					this.polyline = [{
						points: polylinePoints,
						color: '#0091ff',
						width: 6,
						dottedLine: false,
						arrowLine: true,
		
						borderWidth: 1,
						borderColor: '#ffffff'
					}];
				}
			},
			
			// Ëß£ÊûêpolylineÂ≠óÁ¨¶‰∏≤‰∏∫ÂùêÊ†áÁÇπÊï∞ÁªÑ
			parsePolyline(polyline) {
				if (!polyline) return [];
				
				const points = [];
				const coords = polyline.split(';');
				
				for (let i = 0; i < coords.length; i++) {
					const coord = coords[i].split(',');
					if (coord.length === 2) {
						points.push({
							latitude: parseFloat(coord[1]),
							longitude: parseFloat(coord[0])
						});
					}
				}
				return points;
			}
		}
	}
</script>

